@import "../../../styles/vars";
@import "../../../styles/mixins";

.homeAboutDesign {
  @include setDistance(padding-top, 120);
  @include setDistance(padding-bottom, 60);

  &__title {
    text-align: center;
    @include setDistance(margin-bottom, 60)
  }

  &__imagesBlock {
    width: 100%;
    margin: 0 auto;
    max-width: 1396px;
    transition: $transition;
    position: relative;
    z-index: -1;

    @include xxlScreenMedia {
      max-width: 1090px;
    }

    @include xlScreenMedia {
      max-width: 930px;
    }

    @include lgScreenMedia {
      max-width: 794px;
    }
  }

  &__card {
    will-change: transform, opacity;
    position: sticky;
    top: 128px;
    @for $i from 0 through 5 {
      &:nth-child(#{$i + 1}) {
        transform: translateZ(0) translateY($i * 20px);
      }
    }

    @include xxlScreenMedia {
      top: 115px;
    }

    @include xlScreenMedia {
      top: 110px;
    }

    @include lgScreenMedia {
      top: 100px;
    }

    @include smScreenMedia {
      top: 80px;
    }
  }

  &__cardImg {
    width: 100%;
  }


  &__btn {
    display: block;
    margin: 110px auto 0;

    @include smScreenMedia {
      margin-top: 99px;
    }
  }

}
